*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Thasadith', sans-serif;
    outline: none;
}
body{
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background: linear-gradient(107.9deg, #C6EBC9 1.02%, #ACDEAA 19.28%, #70AF85 44.32%, #70AF85 67.28%, #D6F8B8 85.01%, #C6EBC9 101.19%) no-repeat;
    background-size: cover;
}
span{
  color:#ea6557;
}
.box{
    width: 90vw;
    background: rgba(255, 255, 255, 0.5);
    border-radius: 50px;
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
}
.box header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 80px;
}
.box header div h1{
    color: #206A5D;
    font-size: 36px;
    cursor: pointer;
}
.box header div{
    display: flex;
    align-items: center;
}
.box header div ul li{
    display: inline;
    margin: 0 20px;
    cursor: pointer;
}
.box header div ul{
    margin-left: 70px;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
}
.box header button{
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
    background: #5BA19B;
    border: none;
    color: #206A5D;
    width: 120px;
    height: 50px;
    border-radius: 20px;
    cursor: pointer;
    font-size: 18px;
}
.container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 80px;
}
.container div{
    width: 450px;
}
.container div h1{
    color: #206A5D;
    font-size: 72px;
}
.container div p{
    color: #206A5D;
    margin: 30px 0;
}
.container div form{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.container div form input{
    width: 100%;
    margin: 30px 0;
    background: transparent;
    border: 5px solid #5BA19B;
    border-radius: 100px;
    padding: 20px 30px;
    color: #206A5D;
    font-size: 18px;
}
.container div form input::-webkit-input-placeholder{
    color: #5BA19B;
}
.container div form input:-ms-input-placeholder{
    color: #5BA19B;
}
.container div form input::placeholder{
    color: #5BA19B;
}
.container div form button{
    background: #CCEDD2;
    border: 5px solid #f1bd68;
    box-sizing: border-box;
    border-radius: 20px;
    color: #206A5D;
    width: 300px;
    padding: 15px 0;
}
img{

    width: 40%;
    height: 40%;
}
.box .btns{
	position: relative;
	width: 40px;
	height: 40px;
	justify-content: center;
	align-items: center;
    display: none;
}
.btns ion-icon{
	font-size: 1.5em;
	color: #000;
}
.btns .menuToggle ion-icon{
	font-size: 3em;
}
.btns ion-icon:nth-child(2){
	display: none;
}
.btns.active ion-icon:nth-child(2){
	display: block;
    position: fixed;
    z-index: 100;
}
.btns.active ion-icon:nth-child(1){
	display: none;
}
.navigation{
	position: fixed;
	top: 0;
	left: -100%;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(255, 255, 255, 0.3);
    -webkit-backdrop-filter: blur(8px);
            backdrop-filter: blur(8px);
	z-index: 4;
	flex-direction: column;
	transition: 0.5s;
}
.navigation.active{
	left: 0;
}
.navigation li{
	list-style: none;
}
.navigation li{
	display: inline-flex;
	margin: 5px 0;
	font-size: 3.5vh;
	color: #000;
	padding: 5px 20px ;
	border-radius: 40px;
}
.navigation li:hover{
	background: #fff;
}

@media screen and (max-width: 900px) {
    .container{
        flex-direction: column;
        flex-wrap: wrap;
        text-align: center;
    }
    img, .container div{
        width: 40%;
    }
    header ul, header button{
        display: none;
    }
    .box .btns{
        display: flex;
    }
}

